<template>
  <wrapper titulo="Productos">
     <select-cafeteria/>
    <template v-if="$cafeteriaSeleccionada && $cafeteriaSeleccionada.id">
      <v-col cols="12" class="text-end">
        <v-btn rounded outlined color="success">
          <v-icon>mdi-file-excel</v-icon>
          Subir productos desde excel
        </v-btn>
      </v-col>
      <v-row v-if="productosFiltrados.length > 0">
        <v-col cols="12">
          <v-chip-group v-model="categoriaSeleccionada" active-class="deep-purple--text text--accent-4" @change="filtrarProductos">
            <v-chip v-for="(categoria, index) in categorias" :key="index" :value="categoria.id">
              {{ categoria.titulo }}
            </v-chip>
          </v-chip-group>
        </v-col>

        <v-col cols="12" class="text-center">
          <v-text-field v-model="productoParaBuscar" solo rounded label="Buscar productos" prepend-inner-icon="mdi-store-search"/>
        </v-col>

        <v-col v-for="(producto, indexProducto) in productosFiltrados" :key="indexProducto" cols="12" md="4" class="my-2">
          <producto :producto="producto" :editar-producto="() => (abrirParaOperarProducto(producto))"  />
        </v-col>
      </v-row>

      <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
        <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
          <img src="../../../assets/productos_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
        </v-col>
        <v-col cols="12" class="text-center">
          <h2 style="font-family: Nunito;" class="text--disabled">No hay productos en esta cafetería</h2>
        </v-col>
      </v-row>
    </template>

    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../../assets/productos_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">Aún no ha seleccionado cafetería</h2>
      </v-col>
    </v-row>

    <dialogo v-model="dialogOperarProducto" :title="(productoParaOperar.id ? 'Editar' : 'Agregar') + ' producto'">
      <v-form ref="formProducto" lazy-validation>
        <v-row>
          <v-col cols="12" md="6">
            <v-text-field v-model="productoParaOperar.nombre" label="Nombre de producto" :rules="textFieldRules" autofocus />
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="productoParaOperar.costo" label="Costo" type="number" :rules="textFieldRules"
                          :disabled="!!productoParaOperar.id" />
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="productoParaOperar.codigoBarras" label="Codigo de barras"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="productoParaOperar.contenidoNetoValor" label="Contenido neto" type="number"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="productoParaOperar.contenidoNetoTipo" label="Contenido neto tipo"/>
          </v-col>
          <v-col cols="12" md="6">
            <v-select v-model="productoParaOperar.categoria" label="Seleccione categoria" :items="categorias"
                      item-text="titulo" return-object :rules="textFieldRules" />
          </v-col>
          <v-col cols="12" md="6">
            <v-select v-model="productoParaOperar.cafeteria" label="Seleccione cafetería" :items="cafeterias"
                      item-text="nombre" return-object :rules="textFieldRules" />
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field v-model="productoParaOperar.descripcion" label="Descripción"/>
          </v-col>

          <template v-if="productoParaOperar.id">
            <v-col cols="12" md="6">
              <v-checkbox v-model="productoParaOperar.disponible" label="Disponible"/>
            </v-col>
            <v-col cols="12" md="6">
              <v-checkbox v-model="productoParaOperar.activo" label="Activo"/>
            </v-col>
          </template>

        </v-row>
      </v-form>
      <template slot="actions">
        <v-btn text color="primary" plain @click="guardarProducto">Guardar</v-btn>
      </template>
    </dialogo>

    <v-btn fixed color="primary" bottom right fab @click="abrirParaOperarProducto({})">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
  </wrapper>
</template>

<script>

import Producto from "../../Tienda/Producto";
import ProductosService from "../../../services/ProductosService";
import CategoriasService from "@/services/CategoriasService";
import producto from "@/components/Tienda/Producto";
import CafeteriasService from "@/services/CafeteriasService";
import SelectCafeteria from "@/components/UsuarioCafeteria/SelectCafeteria";

export default {
  name: "ProductosCafeteria",
  components: {SelectCafeteria, Producto},
  data(){
    return{
      productos: [],
      productosFiltrados: [],
      dialogOperarProducto: false,
      productoParaOperar: {},
      categorias: [],
      categoriaSeleccionada: null,
      textFieldRules: [v => !!v || 'Este campo es requerido'],
      cafeterias: [],
      dialogSeleccionarCafeteria: false,
      productoParaBuscar: null
    }
  },
  async created() {
    this.$loader = true
    try {
      this.productos = await ProductosService.getProductos();
      this.filtrarProductos();
    } catch (e) {
      this.productos = []
    }

    try {
      this.categorias = await CategoriasService.getCategorias();
    } catch (e) {
      this.categorias = []
    }

    try {
      this.cafeterias = await CafeteriasService.getCafeterias(this.$usuario.idCliente)
    } catch (e) {
      this.cafeterias = []
    }

    this.$loader = false
  },
  watch: {
    productoParaBuscar (val) {
      if (!val || val.length >= 3) {
        this.filtrarProductos();
      }
    },
    $cafeteriaSeleccionada (val) {
      if (val) {
        this.filtrarProductos();
      }
    }
  },
  methods: {
    abrirParaOperarProducto (producto) {
      this.productoParaOperar = producto
      this.dialogOperarProducto = true
      this.$nextTick(() => (this.$refs.formProducto.resetValidation()));
    },
    guardarProducto () {
      if (this.$refs.formProducto.validate()) {
        const vm = this
        vm.$loader = true
        const savePromise = vm.productoParaOperar.id ? ProductosService.update : ProductosService.create;
        vm.productoParaOperar.idCategoria = vm.productoParaOperar.categoria.id

        savePromise(vm.productoParaOperar)
          .then(productoGuardado => {
            if (!vm.productoParaOperar.id)
              vm.productos.push(productoGuardado);

            vm.categoriaSeleccionada = null;
            vm.filtrarProductos();
            vm.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Producto guardado.', true);
          })
          .catch(() => {
            vm.$showAlerta('600', 'error', 'mdi-alert-circle',
              'No fue posible guardar el producto.', true);
            vm.$router.go();
          })
          .finally(() => {
            vm.dialogOperarProducto = false
            vm.$loader = false
          })

      }
    },
    filtrarProductos () {
      this.productosFiltrados = this.productos
        .filter(producto => (!this.categoriaSeleccionada || this.categoriaSeleccionada === producto.categoria.id)
          && (!this.productoParaBuscar || producto.nombre.toLowerCase().includes(this.productoParaBuscar.toLowerCase())) &&
          (producto.idCafeteria === this.$cafeteriaSeleccionada.id))
    },
    seleccionarCafeteria (cafeteria) {
      this.cafeteriaSeleccionada = cafeteria.nombre
      this.dialogSeleccionarCafeteria = false
    }
  }
}
</script>

<style scoped>

</style>

